.calculatedproducts {
  margin: auto;
  text-align: center;
  color: rgb(39, 41, 43);
  background: rgb(39, 41, 43);
  padding: 20px;
  margin-bottom: 30px;
}

.bestalternative {
  margin: 0;
  opacity: 0.8;
  cursor: pointer;
  border: none;
  background-color: transparent;
  border-radius: 10px;
  width: 70px;
  height: 70px;
  background-size: cover;
  background-image: url("data:image/svg+xml;base64,");
  background-repeat: no-repeat;
}

.grow { -webkit-transition: all .2s ease-in-out; -o-transition: all .2s ease-in-out; transition: all .2s ease-in-out; }
.grow:hover { -webkit-transform: scale(1.3); -ms-transform: scale(1.3); transform: scale(1.3); }

.grow2 { -webkit-transition: all .2s ease-in-out; -o-transition: all .2s ease-in-out; transition: all .2s ease-in-out; }
.grow2:hover { -webkit-transform: scale(1.02); -ms-transform: scale(1.02); transform: scale(1.02); }

.grow3 { -webkit-transition: all .2s ease-in-out; -o-transition: all .2s ease-in-out; transition: all .2s ease-in-out; }
.grow3:hover { -webkit-transform: scale(1.1); -ms-transform: scale(1.1); transform: scale(1.1); }

.printedship {
  width: 30%;
  text-align: left;
  margin: auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: wrap;
      flex-flow: wrap;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.bestalternativecontainer {
  width: 45%;
  margin: auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: wrap;
      flex-flow: wrap;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}

.goodorbad {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column;
      flex-flow: column;
  -ms-flex-pack: left;
      justify-content: left;
  margin: 0;
  margin-top: 5px;
}


#bestoption {
  color: #f9a800;
  /*margin: 0;*/
}

#underline {
  text-decoration: underline;
}

#linethrough {
  text-decoration: line-through;
  margin: 0;
  color: rgb(236, 236, 231);
}

#lightfont {
  color: rgb(236, 236, 231);
}

@media (max-width: 600px) {
  .bestalternativecontainer {
    width: 100%;
    -ms-flex-flow: column;
        flex-flow: column;
    -ms-flex-pack: justify;
        justify-content: space-between;
  }
}

@media (min-width:601px) and (max-width:700px){
  .bestalternativecontainer {
    width: 100%;
    -ms-flex-flow: column;
        flex-flow: column;
    -ms-flex-pack: justify;
        justify-content: space-between;
  }
}

@media (min-width:701px) and (max-width:1000px){
  .bestalternativecontainer {
    width: 80%;
  }
}



